@charset "UTF-8";
@font-face {
  font-family: "Oswald";
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-weight: 500;
  font-style: normal;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

*[class*=title],
*[class*=heading],
*[class*=text] {
  margin: 0;
}

*[class*=link] {
  color: inherit;
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.block-center {
  width: calc(100% - 40px);
  margin: 0 auto;
}
@media (min-width: 768px) {
  .block-center {
    width: calc(100% - 60px);
    max-width: 708px;
  }
}
@media (min-width: 1190px) {
  .block-center {
    width: calc(100% - 200px);
    max-width: 1220px;
  }
}

.button {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  background-color: #68b738;
}

.page--column {
  height: 100%;
}

.page__body {
  margin: 0;
  min-width: 280px;
  font-family: "Arial", sans-serif;
  font-weight: 400;
  color: #000000;
  background-color: #ffffff;
  font-size: 14px;
  line-height: 18px;
}
.page__body--column {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.page__header {
  padding: 10px 0 17px;
}

.page__content--column {
  flex-grow: 1;
}

.page__footer {
  padding: 39px 0 22px;
}

.page-header {
  font-family: "Oswald", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
}

.page-header__logo {
  margin: 0;
}

.page-header__nav {
  margin: 0;
}

.title {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
}

.sub-title {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
}

.navigation {
  width: fit-content;
}

.navigation__toggler {
  width: 24px;
  height: 16px;
}

.navigation__menu {
  display: flex;
  flex-direction: column;
}

.menu {
  display: flex;
  flex-direction: column;
  list-style-type: none;
}

.menu__item {
  padding: 22px 0 24px;
}

.menu__link {
  color: inherit;
  font: inherit;
  text-transform: uppercase;
  text-decoration: none;
}

.contacts {
  padding: 0;
  font-size: 14px;
  line-height: 20px;
}

.contacts__info {
  color: #000000;
  background-color: #ffffff;
}

.contacts__heading {
  font-family: "Oswald", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
}

.page-footer {
  width: 100%;
  color: #444444;
  background-color: #f2f2f2;
}

/* Стили для Главная страница */
.main-page {
  background-color: #ffffff;
}

.main-page__section {
  margin-bottom: 20px;
}

.hero {
  padding: 27px 35px 0;
  color: #ffffff;
  background-color: #68b738;
  background-image: url("../img/backgrounds/hero-mobile.webp");
}

.hero__title {
  max-width: 250px;
  margin: 0 auto;
}

.hero__slogan {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
}

.example {
  color: #444444;
  background-color: #f2f2f2;
}

.categories {
  display: flex;
  flex-direction: column;
}

.categories__item {
  margin-top: 20px;
}

.category {
  padding: 21px 18px 19px;
  color: #444444;
  background-color: #f2f2f2;
}

.category__title {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;
  color: #000000;
}

.category__text {
  margin: 0;
}

.category__link {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  color: #000000;
}

/* Стили для Страница каталога */
.catalog-page {
  background-color: #ffffff;
}

/* Стили для Страница формы заказов */
.order-page {
  background-color: #ffffff;
}